p {
  margin: 0;
}
.profile {
  position: relative;
  display: flex;
  justify-content: center;
  background-color: var(--bg-section-color);
}
.avatar {
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--bg-card-color);
}
.description {
  position: relative;
  width: 512px;
  height: auto;
  padding-top: 10px;
  padding-bottom: 80px;
  font-size: 30px;
  font-weight: 600;
  line-height: 1.5;
  background-color: var(--bg-card-color);
  border: 1px solid var(--bg-border-color);
  transition: box-shadow 250ms var(--timing-function);
}
.description:hover,
.description:focus {
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}
.stats {
  position: absolute;
  top: -10px;
  transform: translateY(660px);
  width: 512px;
  height: auto;
  display: flex;
  gap: 10px;
  justify-content: center;

  padding: 5px;
  font-size: 30px;
  box-sizing: border-box;
  color: var(--bg-card-color);
  background-color: rgba(15, 32, 223, 0.4);
}
